export default {
    name: "makeBlock",
  data(){
      return{
        api: this.GLOBAL.baseURL,
        checked:false,
        price:'',
        priceChoose:false,
        money:'',
        isMoney:false,
        currency:[
          {value:0,label:'人民币-￥'},
          {value:1,label:'美元-$'},
          {value:2,label:'日元-J.￥ '}
        ],
        complete:'',
        active_id:'',
        Oreg:/^0|0[.]?[0]∗0/
      }
  },
  methods:{
      cancel(){
        this.price = ''
        this.checked = false
        this.priceChoose=false
      },
    sendActive(){
        if(this.checked == true){
          if(this.Oreg.test(this.price)|| !this.price || !(/((^[1-9]\d*)|^0)(\.\d{0,2}){0,1}$/).test(this.price)){
            this.$message({type:'info',message:'请填写正确金额！（大于1最多保留两位小数的正数）'})
          }else{
              this.$http.post(this.api + '/home/Activity/save_activity',{
                token: localStorage.getItem('token'),
                activity_id: this.active_id,
                book_whole: this.checked,
                low_price: this.price,
                step: 15
              })
                .then(res=>{
                  if(res.data.code == 1){
                    if(!this.complete){
                      this.$emit('changeRouter',{id:16,router:'needVolunter',information: this.active_id,complete: this.complete})
                    }else{
                      this.$message({
                        type: 'success',
                        message: '保存成功！'
                      })
                    }
                  }else if(res.data.code == 3){
                    this.sendActive()
                  }else if(res.data.code == 0){
                    this.$alert(res.data.msg)
                  }
                })
         
          }
      }else{
     
            this.$http.post(this.api + '/home/Activity/save_activity',{
              token: localStorage.getItem('token'),
              activity_id: this.active_id,
              book_whole: this.checked,
              low_price: this.price,
              step: 15
            })
              .then(res=>{
                if(res.data.code == 1){
                  if(!this.complete){
                     this.$emit('changeRouter',{id:16,router:'needVolunter',information: this.active_id,complete:this.complete})
                  }else{
                    this.$message({
                      type: 'success',
                      message: '保存成功！'
                    })
                  }  
                }else if(res.data.code == 3){
                  this.sendActive()
                }else if(res.data.code == 0){
                  this.$alert(res.data.msg)
                }
              })
        
        }
    },
    getActives(){
      this.$http.post(this.api + '/ActivityE',{
        token: localStorage.getItem('token'),
        activity_id: this.active_id,
        visit: 0
      })
        .then(res=>{
          if(res.data.code == 1){
            if(res.data.data.currency >=0){
              this.money = res.data.data.currency
              this.isMoney = true
            }else{
              this.isMoney = false
            }
            if(res.data.data.book_whole){
              this.checked = true
              this.price = res.data.data.low_price
            }
          }else if(res.data.code == 3){
            this.getActives()
          }else if(res.data.code == 0){
            this.$alert(res.data.msg)
          }
        })
    }

  },
  mounted() {
    if(this.active_id){
     this.getActives()
    }
  },
  created(){
    window.scroll(0,0)
      this.complete = this.$route.query.complete
    this.active_id = this.$route.query.information
  }
}



